import React from "react";
import Zoom from "react-reveal/Zoom";
import Fade from "react-reveal/Fade";
import styled from "styled-components";
import atomosphere from "../assets/atomosphere.svg";
const Container = styled.div`
  background: #111;
  padding-bottom: 100px;
`;

export default class Experience extends React.PureComponent {
  componentDidMount() {}
  onMouseOut(e) {
    e.preventDefault();
    e.target.style.transform = "translate(-50%,0)";
  }
  onMouseMove(e) {
    e.preventDefault();
    e.target.style.transform = `translate(calc(-50% + ${e.movementX * 2}px),${
      e.movementY * 2
    }px) scale(1.05)`;
  }
  render() {
    return (
      <Container>
        <div className="header__content">
          <Zoom>
            <div>
              <div className="header__content_line"></div>
            </div>
          </Zoom>
          <Fade bottom>
            <div className="h2 h2-light">Experience</div>
          </Fade>
          <a
            target="_blank"
            rel="noreferrer"
            href="http://atomosphere.in/"
            style={{ color: "#000" }}>
            <Card
              title="Atom O'Sphere"
              img={atomosphere}
              body="Software Intern"
              languages="Learned new technologies like Codeignitor, etc"
            />
          </a>
        </div>
      </Container>
    );
  }
}
const Card = ({ img, title, body, languages, buttons }) => (
  <Fade bottom>
    <div style={{ marginTop: 50 }}>
      <div
        className="card mb-3"
        style={{
          left: "50%",
          transform: "translateX(-50%)",
          textAlign: "left",
        }}>
        <div className="row no-gutters">
          <div
            style={{
              justifyContent: "center",
              alignItems: "center",
              display: "flex",
            }}
            className="col-md-4">
            <img
              src={img}
              className="card-img"
              style={{ height: 200, width: 200, marginTop: 15 }}
              alt={title}
            />
          </div>
          <div className="col-md-8">
            <div className="card-body">
              <h5 className="card-title">{title}</h5>
              <p className="card-text">{body}</p>
              <p className="card-text">
                <small className="text-muted">{languages}</small>
              </p>
              <p>{buttons}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Fade>
);
